/* Styling for the Editor Side. */
.fno-contact-us-block__contact-us-hero-img button {
	position: absolute;
	left: 10px;
	top: 30px;
}

button.components-button.is-button.is-destructive {
	left: 50px;
}

button.components-button.\.fno-contact-us-block__form-contact-submit  {
	padding: 30px;
}

label.components-truncate.components-text.components-input-control__label {
	font-size: 16px;
	line-height: 30px;
	color: #7a838d;
	font-weight: 500;
	font-family: 'Urbanist', sans-serif;
	text-transform: none;
}

.fno-contact-us-block__form-contact-product-area .components-input-control__container .components-select-control__input,
.fno-contact-us-block__form-contact-country .components-input-control__container .components-select-control__input {
	color: black !important;
	border-bottom: 1.5px solid lightgray !important;
	font-size: 18px !important;
	line-height: 30px;
	font-weight: 400;
	font-family: 'be_vietnam_proregular', sans-serif !important;
	border: none !important;
	outline: none;
}
